$darkBlue: #7abc;
$yellow: #d8da79;
$lightBrown: #c29486;
$green: #14c9a8;

/* MIXINS */
@mixin label($color, $width: 100px, $radius: 0) {
  text-align: center;
  border-radius: $radius;
  width: $width;
  height: 20px;
  background-color: $color;
}

%color-theme {
  background-color: palevioletred;
  font-size: 100px;
